body {
  margin: 0;
  padding: 0;
  font-family: sans-serif; }

.map-wrapper {
  margin: 10px auto;
  width: 1000px; }
  .map-wrapper .google-map-container {
    height: 600px; }
  .map-wrapper .list {
    width: 150px;
    background-color: #FFFFFF;
    padding: 5px 5px 5px 5px; }
    .map-wrapper .list .list-item {
      font-size: 14px;
      padding: 5px 5px 5px 5px;
      border: 2px solid black; }
    .map-wrapper .list .list-item:hover {
      background-color: #e1e3e8;
      cursor: pointer; }
  .map-wrapper .popup {
    position: absolute;
    top: 50px;
    left: 30px;
    border: 1px solid black;
    padding: 10px 10px 10px 10px;
    font-size: 12px; }
  .map-wrapper .dropdown {
    position: absolute;
    top: 50px;
    right: 30px;
    width: 300px; }



